import React from "react"
import {useState} from "react"


export default function CalculatorScreen() {

const [expression, setexpression] = useState(0)
const [answer, setanswer] = useState(0)

const display = (symbol) =>{
  setexpression((prev) => prev + symbol)
  if(expression?.length-1 === "="){
    if(/[0-9.]/.test(symbol)){
      setexpression(symbol)
    }else{
      setexpression(answer + symbol)
    }
  }
}
const calculate = () =>{
  setanswer(eval(expression))
  setexpression(prev => prev + "=")
}
const allclear = ()=>{
  setexpression("")
  setanswer(0)
}
  return (
      <>
        <div className="container">
          <div style={{background:"black",padding:"5px"}}>
            <div className="grid">
              <div className="dis">
                <input type="text" value={expression} placeholder="0" disabled/>
                <div className="display">{answer}</div>
              </div>
                <div onClick={allclear} className="padButtom clear">AC</div>
                <div onClick={()=>display("/")} className="padButtom divide">/</div>
                <div onClick={()=>display("*")} className="padButtom multiply">x</div>
                <div onClick={()=>display("7")} className="padButtom seven dark-gray">7</div>
                <div onClick={()=>display("8")} className="padButtom eight dark-gray">8</div>
                <div onClick={()=>display("9")} className="padButtom nine dark-gray">9</div>
                <div onClick={()=>display("-")} className="padButtom subtract">-</div>
                <div onClick={()=>display("4")} className="padButtom four dark-gray">4</div>
                <div onClick={()=>display("5")} className="padButtom five dark-gray">5</div>
                <div onClick={()=>display("6")} className="padButtom six dark-gray">6</div>
                <div onClick={()=>display("+")} className="padButtom plus">+</div>
                <div onClick={()=>display("1")} className="padButtom one dark-gray">1</div>
                <div onClick={()=>display("2")} className="padButtom two dark-gray">2</div>
                <div onClick={()=>display("3")} className="padButtom three dark-gray">3</div>
                <div onClick={calculate} className="padButtom equals">=</div>
                <div onClick={()=>display("0")} className="padButtom zero dark-gray">0</div>
                <div onClick={()=>display(".")} className="padButtom decimal dark-gray">.</div>
            </div>
            </div>
        </div>
      </>
  );
}
